.Midsec{
    margin: 0px 40px
}
.mid_head{
    color: #4a4a4a;
    margin-top: 50px;
    margin-bottom: 20px;
    /* margin-left: 20px;
    margin-right: 20px; */
    font-size: 22px;
    font-weight: 600px;
}
.pop_venu{
    color: #4a4a4a;
    font-size: 22px;
    font-weight: 500;
}

.mid_container{
    overflow-x: scroll;
    padding-left: 30px;
    padding-right: 30px;
}
/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 2px #e72e77;
    border-radius: 10px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #e72e77;
    border-radius: 10px;
  }
.display_inline{
    display: inline-flex;
    margin: 0px 0px 20px;
}
.card_height{
    height: 298px;
    overflow: hidden;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 5px 0
}
.card_height img{
    height: 261px;
    object-fit: cover;
    /* border-radius: 5px; */
}
.card_height img:hover{
    transition: all 0.5s ease-in-out 0s;
    transform: scale(1.3);

}
.card_width{
    height: 120px!important;
    width: 400px!important;
    border: none;
    overflow: hidden;
}
.card_width img{
    height: 120px!important;
    width: 180px!important;
    object-fit: cover;
    border-radius: 6px;
}
.card-text {
    font-size: 14px;
    -webkit-line-clamp: 2;
    color: #e72e77;
}
